header, nav {
    display: block;
}
.nav-theme {
    background-color: #0f0f0ff8;
    font-size: 15px;
}
  
.animate-navbar {
    box-shadow: 1px 1px 1px #222;
    animation: moveDown 0.5s ease-in-out;
}

@keyframes moveDown {
    from {
      transform: translateY(-5rem);
    }
    to {
      transform: translateY(0rem);
    }
}
h1.h1Nav{
    margin-bottom: 0em;
    font-size: 30px;
    color: #61dafb; 
}

@media screen and (max-width: 780px) {
  h1.h1Nav {
    font-size: 20px;
  }
}